import React, { useContext, useMemo } from 'react';
import PropTypes from 'prop-types';
import ThemeContext from './../theme/ThemeContext';
import Box from '../Box';
import useStyles from './../hooks/useStyles';
import {
	alignContent,
	alignItems,
	alignSelf,
	flex,
	flexBasis,
	justifyContent,
	justifySelf,
	order,
	style,
} from 'styled-system';

export const flexDisplay = props =>
	props.child ? null : props.inline ? { display: 'inline-flex' } : { display: 'flex' };

export const flexDirection = style({
	prop: 'direction',
	cssProperty: 'flexDirection',
	transformValue: n => (n === 'col' || n === 'column' ? 'column' : 'row'),
});

export const flexGrow = style({
	prop: 'flexGrow',
});

export const flexShrink = style({
	prop: 'flexShrink',
});

export const flexWrap = style({
	prop: 'flexWrap',
});

const Flex = React.forwardRef((props, ref) => {
	const { child, ...passThru } = props;
	const { theme } = useContext(ThemeContext);
	const styles = useStyles(
		[
			alignContent,
			alignItems,
			alignSelf,
			flex,
			flexBasis,
			flexDisplay,
			flexDirection,
			flexGrow,
			flexShrink,
			flexWrap,
			justifyContent,
			justifySelf,
			order,
		],
		{ ...props, theme },
	);

	return (
		<Box ref={ref} styles={styles} {...passThru}>
			{props.children}
		</Box>
	);
});

Flex.displayName = 'Flex';

Flex.propTypes = {
	...alignContent.propTypes,
	...alignItems.propTypes,
	...alignSelf.propTypes,
	...flex.propTypes,
	...flexBasis.propTypes,
	...flexDirection.propTypes,
	...flexGrow.propTyeps,
	...flexShrink.propTypes,
	...flexWrap.propTypes,
	...justifyContent.propTypes,
	...justifySelf.propTypes,
	...order.propTypes,
	...{
		child: PropTypes.bool,
		inline: PropTypes.bool,
		styles: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
	},
};

Flex.defaultProps = {
	inline: false,
};

export default Flex;



