import React, { useContext, useMemo } from 'react';
import PropTypes from 'prop-types';
import ThemeContext from './../theme/ThemeContext';
import cn from './../theme/className';
import useStyles from './../hooks/useStyles';
import {
	borders,
	borderColor as borderColorParser,
	bottom as bottomParser,
	display as displayParser,
	gridGap,
	gridColumnGap,
	gridRowGap,
	gridColumn,
	gridRow,
	gridAutoFlow,
	gridAutoColumns,
	gridAutoRows,
	gridTemplateColumns,
	gridTemplateRows,
	gridTemplateAreas,
	gridArea,
	left as leftParser,
	opacity as opacityParser,
	position as positionParser,
	right as rightParser,
	sizeHeight,
	sizeWidth,
	style,
	top as topParser,
	zIndex as zIndexParser,
} from 'styled-system';
import {
	bgColor,
	borderRadius,
	fontSize,
	height,
	maxHeight,
	maxWidth,
	minHeight,
	minWidth,
	textColor,
	space,
	width,
} from './../styles';

const elevation = style({
	prop: 'elevation',
	key: 'elevation',
});

const Box = React.forwardRef((props, ref) => {
	const {
		className: classNameProp,
		inline,
		align,
		alignContent,
		alignItems,
		alignSelf,
		bg,
		border,
		borderTop,
		borderRight,
		borderBottom,
		borderLeft,
		borderColor,
		bottom,
		color,
		direction,
		display,
		elevation,
		flex,
		flexDirection,
		flexWrap,
		fontSize,
		gridGap,
		gridColumnGap,
		gridRowGap,
		gridColumn,
		gridRow,
		gridAutoFlow,
		gridAutoColumns,
		gridAutoRows,
		gridTemplateColumns,
		gridTemplateRows,
		gridTemplateAreas,
		gridArea,
		left,
		h,
		hMax,
		hMin,
		is,
		justify,
		justifyContent,
		justifySelf,
		m,
		mb,
		ml,
		mr,
		mt,
		mx,
		my,
		order,
		opacity,
		p,
		pb,
		pl,
		position,
		pr,
		pt,
		px,
		py,
		radius,
		right,
		size,
		styles: stylesProp,
		top,
		w,
		wMax,
		wMin,
		zIndex,
		...passThru
	} = props;
	const { theme } = useContext(ThemeContext);
	const Component = props.is;
	const styles = useStyles(
		[
			bgColor,
			borders,
			borderColorParser,
			borderRadius,
			bottomParser,
			displayParser,
			elevation,
			fontSize,
			gridGap,
			gridColumnGap,
			gridRowGap,
			gridColumn,
			gridRow,
			gridAutoFlow,
			gridAutoColumns,
			gridAutoRows,
			gridTemplateColumns,
			gridTemplateRows,
			gridTemplateAreas,
			gridArea,
			leftParser,
			height,
			maxHeight,
			maxWidth,
			minHeight,
			minWidth,
			opacityParser,
			positionParser,
			rightParser,
			sizeHeight,
			sizeWidth,
			space,
			textColor,
			topParser,
			width,
			zIndexParser,
		],
		{
			bg,
			border,
			borderTop,
			borderRight,
			borderBottom,
			borderLeft,
			borderColor,
			bottom,
			color,
			display,
			elevation,
			fontSize,
			gridGap,
			gridColumnGap,
			gridRowGap,
			gridColumn,
			gridRow,
			gridAutoFlow,
			gridAutoColumns,
			gridAutoRows,
			gridTemplateColumns,
			gridTemplateRows,
			gridTemplateAreas,
			gridArea,
			left,
			h,
			hMax,
			hMin,
			m,
			mb,
			ml,
			mr,
			mt,
			mx,
			my,
			opacity,
			p,
			pb,
			pl,
			position,
			pr,
			pt,
			px,
			py,
			right,
			size,
			stylesProp,
			theme,
			top,
			w,
			wMax,
			wMin,
			zIndex,
		},
	);
	const className = useMemo(() => cn(classNameProp, styles), [classNameProp, styles]);
	return (
		<Component ref={ref} className={className} {...passThru}>
			{props.children}
		</Component>
	);
});

Box.displayName = 'Box';

Box.propTypes = {
	...bgColor.propTypes,
	...borders.propTypes,
	...borderColorParser.propTypes,
	...borderRadius.propTypes,
	...bottomParser.propTypes,
	...displayParser.propTypes,
	...elevation.propTypes,
	...fontSize.propTypes,
	...gridGap.propTypes,
	...gridColumnGap.propTypes,
	...gridRowGap.propTypes,
	...gridColumn.propTypes,
	...gridRow.propTypes,
	...gridAutoFlow.propTypes,
	...gridAutoColumns.propTypes,
	...gridAutoRows.propTypes,
	...gridTemplateColumns.propTypes,
	...gridTemplateRows.propTypes,
	...gridTemplateAreas.propTypes,
	...gridArea.propTypes,
	...leftParser.propTypes,
	...height.propTypes,
	...maxHeight.propTypes,
	...maxWidth.propTypes,
	...minHeight.propTypes,
	...minWidth.propTypes,
	...opacityParser.propTypes,
	...positionParser.propTypes,
	...rightParser.propTypes,
	...sizeHeight.propTypes,
	...sizeWidth.propTypes,
	...space.propTypes,
	...textColor.propTypes,
	...topParser.propTypes,
	...width.propTypes,
	...zIndexParser.propTypes,
	...{
		is: PropTypes.node,
		styles: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
	},
};

Box.defaultProps = {
	is: 'div',
};

export default Box;



