import React, { useContext, useMemo } from 'react';
import PropTypes from 'prop-types';
import useStyles from './../hooks/useStyles';
import ThemeContext from './../theme/ThemeContext';
import cn from './../theme/className';
import { textAlign, variant } from 'styled-system';
import { textColor, fontFamily, fontSize, fontWeight, lineHeight, space } from './../styles';

const TAG_MAP = {
	h1: 'h1',
	h2: 'h2',
	h3: 'h3',
	h4: 'h4',
	h5: 'h5',
	h6: 'h6',
	subtitle1: 'h6',
	subtitle2: 'h6',
	body1: 'p',
	body2: 'p',
	caption: 'span',
	overline: 'span',
};

export const variants = variant({
	key: 'typography.variants',
});

const getColorStyles = props => {
	switch (props.color) {
		case 'primary':
		case 'secondary':
		case 'textPrimary':
		case 'textSecondary':
		case 'error':
			return {
				rootStyles: {
					color: props.theme.palette[props.color].main,
				},
			};

		case 'inherit':
			return {
				rootStyles: {
					color: 'inherit',
				},
			};

		default:
			return null;
	}
};

export const getBaseStyles = props => ({
	rootStyles: {
		margin: '0px',
		...variants(props),
		...fontFamily(props),
		...fontSize(props),
		...fontWeight(props),
		...lineHeight(props),
		...textAlign(props),
		...textColor(props),
		...space(props),
	},
});

function Typography(props) {
	const {
		color,
		className: classNameProp,
		component,
		font,
		lineHeight,
		m,
		mb,
		ml,
		mr,
		mt,
		mx,
		my,
		p,
		paragraph,
		pb,
		pl,
		pr,
		pt,
		px,
		py,
		size,
		variant,
		weight,
		styles,
	} = props;
	const { theme } = useContext(ThemeContext);
	const { rootStyles } = useStyles([getBaseStyles, getColorStyles], {
		color,
		font,
		lineHeight,
		m,
		mb,
		ml,
		mr,
		mt,
		mx,
		my,
		paragraph,
		size,
		styles,
		theme,
		variant,
		weight,
	});
	const className = useMemo(() => cn(classNameProp, rootStyles), [classNameProp, rootStyles]);
	const Component = component ? component : paragraph ? 'p' : TAG_MAP[variant] || 'span';

	return <Component className={className}>{props.children}</Component>;
}

Typography.displayName = 'Typography';

Typography.propTypes = {
	...fontFamily.propTypes,
	...fontSize.propTypes,
	...fontWeight.propTypes,
	...lineHeight.propTypes,
	...space.propTypes,
	...textAlign.propTypes,
	...textColor.propTypes,
	...variants.propTypes,
	...{
		paragraph: PropTypes.bool,
	},
	styles: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
};

Typography.defaultProps = {
	color: 'default',
	variant: 'body1',
};

export default Typography;



